import React from 'react'
import ProductComponent from './ProductComponent';

const productListComponent = (props) => {
    var cartProductList = props.products.map(productDTO => {
       return( 
         <div key={productDTO.id}>
            <ProductComponent 
            id={productDTO.id}
            // handleIncrement={productDTO.handleIncrement}
            // handleDecrement={productDTO.handleDecrement}
            image_url={productDTO.image_url}
            product_name={productDTO.product_name}
            price={productDTO.price}
            brand={productDTO.brand}
            />
   <hr></hr>
   </div>
       );
    });

    return (<div>{cartProductList}</div>);
}

export default productListComponent;